/* NFTImageFetcher.css */

body {
    background-color: lightblue;
    font-family: Arial, sans-serif;
    margin: 0;
    padding: 0;
}

.container {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    background-color: lightblue;
    color: black;
    padding: 20px;
    margin-top: 20px;
    min-height: 100vh;
    position: relative; /* Position relative for absolute positioning */
}

.header {
    color: rgb(80, 5, 74);
    margin-bottom: 10px;
    text-align: center;
    display: flex;
    justify-content: space-between; /* Space between items */
    align-items: center; /* Center vertically */
    width: 100%;
}

.header h1 {
    margin: 0;
    flex: 1; /* Take remaining space */
    text-align: center;
}

.header-buttons {
    display: flex;
    gap: 10px;
}

.connect-button, .toggle-button {
    background-color: rgb(80, 5, 74);
    color: white;
    border: none;
    padding: 8px 16px;
    cursor: pointer;
    transition: background-color 0.3s ease;
}

.connect-button:hover, .toggle-button:hover {
    background-color: rgba(80, 5, 74, 0.8);
}

.content {
    display: flex;
    justify-content: center;
    align-items: flex-start;
    width: 100%;
    margin-top: 20px;
}

.main {
    flex: 1;
}

.image-container {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.image-row {
    display: flex;
    justify-content: space-around;
    width: 100%;
    margin-bottom: 20px;
}

.image-item {
    margin: 10px;
    padding: 10px;
    text-align: center;
}

.image-item img {
    width: 200px;
    height: 200px;
    object-fit: cover;
}

.image-details {
    margin-top: 10px;
}

.buy-button {
    background-color: rgb(80, 5, 74);
    color: white;
    border: none;
    padding: 8px 16px;
    cursor: pointer;
    transition: background-color 0.3s ease;
}

.buy-button:hover {
    background-color: rgba(80, 5, 74, 0.8);
}

.sidebar {
    flex: 0 0 250px;
    background-color: #f0f0f0;
    border-left: 1px solid #ccc;
    padding: 20px;
    box-shadow: 2px 0 5px rgba(0, 0, 0, 0.1);
    position: absolute; /* Position sidebar absolutely */
    top: 0; /* Align to top */
    right: 0; /* Align to right */
}

.sidebar-header {
    margin-bottom: 10px;
    text-align: center;
}

.sidebar-content {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.sidebar-item {
    margin-bottom: 20px;
    text-align: center;
}

.sidebar-item img {
    width: 100px;
    height: 100px;
    object-fit: cover;
}

.sidebar-details {
    margin-top: 10px;
}

/* Styles for 'My NFTs' section */

.my-nfts-container {
    position: absolute;
    top: 60px; /* Adjust this value to position it below the header */
    right: 20px; /* Adjust this value to position it from the right */
    background-color: #fff;
    border: 1px solid #ccc;
    padding: 20px;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
    z-index: 100; /* Ensure it's above other content */
}

.my-nfts-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 10px;
}

.my-nfts-header h2 {
    margin: 0;
}

.my-nfts-close-button {
    background-color: rgb(80, 5, 74);
    color: white;
    border: none;
    padding: 8px 16px;
    cursor: pointer;
    transition: background-color 0.3s ease;
}

.my-nfts-close-button:hover {
    background-color: rgba(80, 5, 74, 0.8);
}

.my-nft-item {
    margin-bottom: 20px;
    text-align: center;
}

.my-nft-item img {
    width: 150px; /* Adjust the width as per your design */
    height: 150px; /* Adjust the height as per your design */
    object-fit: cover;
}

.my-nft-details {
    margin-top: 10px;
}
